.currency {
    width: 200px;
    margin: 20px;
    text-align: center;
    padding: 15px;
}

.currency p, .currency h3 {
    margin: 0;
    padding: 0;
}

.currency .header {
    color: white;
    opacity: 0.7;
    font-size: 14px;
    margin-bottom: 5px;
}

.currency .type {
    font-size: 30px;
    font-weight: 500;
    color: white;
}

.currency .value {
    color: white;
    font-size: 25px;
    font-weight: 700;
}

.currency .percentChange {
    font-size: 12px;
    color: white;
    opacity: 0.7;
}


