header {
    position: relative;
    height: 600px;
    color: white;
    z-index: 0;
    background-image: -webkit-radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
}

img.sine {
    position: absolute;
    bottom: -1px;
    width: 100%;
}

div.hexagon {
    position: absolute;
    bottom: 0;
    z-index: -1;
    width: 100vw;
    height: 100%;
    background-image: url("../../Images/hexagonPattern.png");
    opacity: 0.03;
}

header img.logo {
    width: 64px;
    position: absolute;
    left: 12%;
    top: 3em;
}

@media screen and (max-width: 1000px) {
    img.logo {
        top: 1em;
    }
}


