.mining {
    position: relative;
    top: -12em;
    z-index: -1;
    width: 100%;
    padding-top: 9em;
    padding-bottom: 9em;
    background-image: -webkit-linear-gradient(180deg, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-linear-gradient(180deg, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -webkit-gradient(linear, left top, left bottom, from(hsl(244, 31%, 53%)), color-stop(80%, hsl(266, 61%, 42%)));
    background-image: -webkit-linear-gradient(top, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-linear-gradient(top, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: linear-gradient(180deg, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-attachment: fixed;
}

.mining p {
    line-height: 22px;
    font-size: 14px;
    color: white;
    opacity: 0.8;
}

.mining h3 {
    font-weight: 700;
    font-size: 14px;
    color: white;
    opacity: 0.8;
}

.mining > div {
    width: 60%;
    margin-left: 20%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.mining .mine > img {
    width: 96px;
    height: 96px;
    opacity: 0.4;
}

.mining .info {
    width: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
}

.mining .info span {
    font-size: 10px;
    display: block;
    opacity: 0.5;
}


@media screen and (max-width: 900px) {
    .mining > div {
        width: 90%;
        margin-left: 5%;
    }
}

@media screen and (max-width: 700px) {
    .mining .info {
        width: 100%;
    }
}

.mining .skewUpper {
    position: absolute;
    bottom: -1px;
    width: 100%;
}

.mining .skewLower {
    position: absolute;
    top: -1px;
    width: 100%;
}

.mining .mine {
    margin-right: 5%;
    border-radius: 100%;
    padding: 1em;
}

.mining .stars {
    position: relative;
}

.mining .star {
    position: absolute;
    width: 12px;
    height: 12px;
}

.starOne {
    top: -20px;
    left: -20px;
    -webkit-animation: GLOW 10s infinite;
    animation: GLOW 10s infinite;
}

.starTwo {
    top: -70px;
    left: -30px;
    -webkit-animation: GLOW 7s infinite;
    animation: GLOW 7s infinite;
}

.starThree {
    top: -50px;
    left: 100px;
    -webkit-animation: GLOW 4s infinite;
    animation: GLOW 4s infinite;
}

.starFour {
    top: -120px;
    left: 20px;
    -webkit-animation: GLOW 12s infinite;
    animation: GLOW 12s infinite;
}

.starFive {
    top: -30px;
    left: 120px;
    -webkit-animation: GLOW 15s infinite;
    animation: GLOW 15s infinite;
}

.starSix {
    top: -90px;
    left: 120px;
    -webkit-animation: GLOW 20s infinite;
    animation: GLOW 20s infinite;
}

@keyframes GLOW {
    0% { opacity: 0; }
    50% { opacity: 0.4; }
    100% { opacity: 0; }
}

@-webkit-keyframes GLOW {
    0% { opacity: 0; }
    50% { opacity: 0.4; }
    100% { opacity: 0; }
}


