.plansRow {
    width: 90%;
    margin-left: 5%;
    position: relative;
    top: -120px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.plan {
    width: 280px;
    background: white;
    -webkit-box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25), 0px 0px 0px 1px rgb(223, 223, 223) inset;
            box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25), 0px 0px 0px 1px rgb(223, 223, 223) inset;
    border-top: 2px solid rgb(168, 18, 206);
    border-bottom: 2px solid rgb(221, 221, 221);
    border-radius: 10px;
    padding-bottom: 15px;
    margin: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
}

.plan:hover {
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
    -webkit-box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.35), 0px 0px 0px 1px rgb(223, 223, 223) inset;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.35), 0px 0px 0px 1px rgb(223, 223, 223) inset;

    -webkit-transform: translate3d(0, -7px, 0);

            transform: translate3d(0, -7px, 0);
}

.plan-header {
    text-align: center;
    color: rgb(168, 18, 206);
    padding-left: 25px;
    padding-right: 25px;
}

.plan-info {
    color: #3b3b3b;
    line-height: 22px;
    font-size: 14px;
    padding-left: 25px;
    padding-right: 25px;
}

.plan-header h3 .fa-clock {
    -webkit-animation: spin 10s infinite;
    animation: spin 10s infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

.plan-header h3 .fa-hands-helping {
    -webkit-animation: shake 2s infinite;
    animation: shake 2s infinite;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
    25% { -webkit-transform: translate3d(0, -2px, 0); transform: translate3d(0, -2px, 0) }
    50% { -webkit-transform: translate3d(0, 2px, 0); transform: translate3d(0, 2px, 0) }
    75% { -webkit-transform: translate3d(0, -2px, 0); transform: translate3d(0, -2px, 0) }
    100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
}

@keyframes shake {
    0% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
    25% { -webkit-transform: translate3d(0, -2px, 0); transform: translate3d(0, -2px, 0) }
    50% { -webkit-transform: translate3d(0, 2px, 0); transform: translate3d(0, 2px, 0) }
    75% { -webkit-transform: translate3d(0, -2px, 0); transform: translate3d(0, -2px, 0) }
    100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
}

.plan-header h3 .fa-credit-card {
    -webkit-animation: slide 6s infinite;
    animation: slide 6s infinite;
}

@-webkit-keyframes slide {
    0% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
    25% { -webkit-transform: translate3d(-4px, 0, 0); transform: translate3d(-4px, 0, 0) }
    50% { -webkit-transform: translate3d(4px, 0, 0); transform: translate3d(4px, 0, 0) }
    75% { -webkit-transform: translate3d(-4px, 0, 0); transform: translate3d(-4px, 0, 0) }
    100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
}

@keyframes slide {
    0% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
    25% { -webkit-transform: translate3d(-4px, 0, 0); transform: translate3d(-4px, 0, 0) }
    50% { -webkit-transform: translate3d(4px, 0, 0); transform: translate3d(4px, 0, 0) }
    75% { -webkit-transform: translate3d(-4px, 0, 0); transform: translate3d(-4px, 0, 0) }
    100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0) }
}

.fa-lock {
    -webkit-animation: unlock 5s infinite;
            animation: unlock 5s infinite;
}

.fa-unlock {
    position: relative;
    left: -16px;
    opacity: 0;
    -webkit-animation: lock 5s infinite;
            animation: lock 5s infinite;
}

@-webkit-keyframes lock {
    0% { opacity: 0 }
    50% { opacity: 1 }
    100% { opacity: 0 }
}

@keyframes lock {
    0% { opacity: 0 }
    50% { opacity: 1 }
    100% { opacity: 0 }
}

@-webkit-keyframes unlock {
    0% { opacity: 1 }
    50% { opacity: 0 }
    100% { opacity: 1 }
}

@keyframes unlock {
    0% { opacity: 1 }
    50% { opacity: 0 }
    100% { opacity: 1 }
}

.fa-user-secret {
    -webkit-animation: hide 10s infinite;
            animation: hide 10s infinite;
}

@-webkit-keyframes hide {
    0% { opacity: 0.25 }
    50% { opacity: 1 }
    100% { opacity: 0.25 }
}

@keyframes hide {
    0% { opacity: 0.25 }
    50% { opacity: 1 }
    100% { opacity: 0.25 }
}


