.hero {
    width: 80%;
    margin-left: 10%;
    font-weight: 500;
    position: relative;
    margin-top: 8em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
}

@media screen and (max-width: 800px) {
    .hero {
        -ms-flex-pack: center;
            justify-content: center;
    }
}

.hero button {
    font-family: Montserrat;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
    outline: 0;
    border: 0;
    border-radius: 20px;
    padding: 17px 42px;
    margin: 10px;
    color: white;
    -webkit-box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.2);
            box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.2);
}

button.why {
    background: -webkit-linear-gradient(315deg, rgb(168, 18, 206), rgb(137, 18, 206));
    background: -o-linear-gradient(315deg, rgb(168, 18, 206), rgb(137, 18, 206));
    background: linear-gradient(135deg, rgb(168, 18, 206), rgb(137, 18, 206));
}

button.discover {
    background: -webkit-linear-gradient(315deg, white, rgb(204, 204, 204));
    background: -o-linear-gradient(315deg, white, rgb(204, 204, 204));
    background: linear-gradient(135deg, white, rgb(204, 204, 204));
    color: rgb(168, 18, 206);
}

button.why:hover {
    background: -webkit-linear-gradient(315deg, hsl(288, 84%, 54%), hsl(278, 84%, 54%));
    background: -o-linear-gradient(315deg, hsl(288, 84%, 54%), hsl(278, 84%, 54%));
    background: linear-gradient(135deg, hsl(288, 84%, 54%), hsl(278, 84%, 54%));
    -webkit-transform: translateY(-2px);
        -ms-transform: translateY(-2px);
            transform: translateY(-2px);
}

button.discover:hover {
    -webkit-transform: translateY(-2px);
        -ms-transform: translateY(-2px);
            transform: translateY(-2px);
}

.hero > div.info {
    line-height: 22px;
    min-width: 300px;
    max-width: 800px;
    margin-left: 20px;
    opacity: 0.8;
}


