.guides {
    width: 800px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: -120px;
    margin-bottom: 120px;
}

@media screen and (max-width: 900px) {
    .guides {
        width: 90%;
        margin-left: 5%;
    }
}

.guides h2 {
    color: rgb(168, 18, 206);
    text-transform: uppercase;
    letter-spacing: 1px;
    padding-left: 5px;
    padding-right: 5px;
}

.guides p {
    color: #3b3b3b;
    font-size: 14px;
    line-height: 22px;
    font-weight: 500;
}

.guides p a {
    text-decoration: none;
    display: block;
    font-size: 12px;
}

.guides .icons {
    width: 90%;
    margin-left: 5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.guides .icons svg {
    font-size: 3em;
    margin-left: 5px;
    margin-right: 5px;
}

.guides .icons svg.fa-exchange-alt, .guides .icons .fa-long-arrow-alt-left, .guides .icons .fa-long-arrow-alt-right {
    color: rgb(168, 18, 206);
    font-size: 2em;
}

.guides .icons .sm {
    font-size: 1.5em;
}

.guides .equation {
    width: 200px;
    height: auto;
}

.guides .info {
    margin-top: 10px;
    font-size: 10px;
    display: block;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}



