.currencyList {
    display: -ms-flexbox;
    display: flex;
    width: 90%;
    padding: 0 5%;
    margin: 0 auto;

    -ms-flex-direction: row;

        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.currencies {
    width: 90%;
    padding: 2em 5%;
    margin-bottom: 200px;
    background-image: -webkit-linear-gradient(180deg, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-linear-gradient(180deg, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -webkit-gradient(linear, left top, left bottom, from(hsl(244, 31%, 53%)), color-stop(80%, hsl(266, 61%, 42%)));
    background-image: -webkit-linear-gradient(top, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-linear-gradient(top, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: linear-gradient(180deg, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-attachment: fixed;
    text-align: center;
}

.currencies select {
    padding: 0.5em;
    border: none;
    outline: none;
    background: none;
    color: white;
    font-size: 14px;
}

.currencies option {
    color: #3b3b3b;
}

.currencies > p {
    color: white;
    opacity: 0.7;
}

.currencies button {
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
    margin-bottom: 20px;
    padding: 10px 20px;
    color: white;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 2px;
}

.currencies button.active {
    border-bottom: 3px solid rgb(168, 18, 206);
}

.currencies button:hover {
    border-bottom: 3px solid rgba(168, 18, 206, 0.5);
}


