footer {
    background-image: -webkit-radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    padding-top: 4em;
    padding-bottom: 4em;
    margin-top: 5em;
    position: relative;
}

.foot {
    width: 90%;
    margin-left: 5%;
}

.foot svg {
    font-size: 4em;
    opacity: 0.4;
}

footer img.sineTop {
    position: absolute;
    top: -1px;
    width: 100%;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}

footer .logo {
    width: 64px;
    position: absolute;
    bottom: 15px;
    left: 50%;
    -webkit-transform: translate(-50%);
        -ms-transform: translate(-50%);
            transform: translate(-50%);
    opacity: 0.5;
}


