nav {
    padding-top: 2em;
    padding-right: 8em;
    text-align: right;
}

@media screen and (max-width: 1000px) {
    nav {
        padding-right: 1em;
        padding-top: 1em;
    }
}

nav ul {
    background: white;
    display: inline-block;
    padding: 20px 30px;
    border-radius: 20px;
    border-bottom: 2px solid rgb(168, 18, 206);
}

nav li {
    display: inline-block;
}

nav a {
    padding: 0;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: 25px;
    margin-right: 25px;
    text-decoration: none;
    color: rgb(168, 18, 206);
    font-weight: 700;
    font-size: 14px;
}

nav li a:hover {
    color: hsl(244, 31%, 53%);
}

.hamburger .menuButton {
    z-index: 1005;
    color: rgb(168, 18, 206);
    font-size: 2em;
    background: none;
    border: none;
    outline: none;
    -webkit-transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
    -o-transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
    transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
}

.hamburger > .menu {
    width: 320px;
    height: 100vh;
    background: white;
    position: fixed;
    top: 0;
    right: 0;
    -webkit-transition: -webkit-transform .3s cubic-bezier(0, .52, 0, 1);
    transition: -webkit-transform .3s cubic-bezier(0, .52, 0, 1);
    -o-transition: transform .3s cubic-bezier(0, .52, 0, 1);
    transition: transform .3s cubic-bezier(0, .52, 0, 1);
    transition: transform .3s cubic-bezier(0, .52, 0, 1), -webkit-transform .3s cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 1000;
}

.hamburger > .hide {
    -webkit-transform: translate3d(+320px, 0, 0);
            transform: translate3d(+320px, 0, 0);
}

.hamburger > .show {
    -webkit-transform: translate3d(0px, 0, 0);
            transform: translate3d(0px, 0, 0);
    overflow: hidden;
}

.hamburger > .menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.hamburger > .menu > a:first-of-type {
    margin-top: 2em;
}

.hamburger > .menu > a {
    padding: 0.75em;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
    padding-left: 50px;
}

.menuCover {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 999;
    background: rgba(0, 0, 0, 0.2);
}

.menuCover.show {
    display: block;
}

.menuCover.hide {
    display: none;
}


