.discover-modal {
    width: 100%;
}

.discover-modal .modal {
    position: fixed;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 10px;
    z-index: 1011;
    -webkit-box-shadow: 0px 2px 35px rgba(0, 0, 0, 0.5);
            box-shadow: 0px 2px 35px rgba(0, 0, 0, 0.5);
    padding: 3em;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 600px;
    background-image: -webkit-radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: -o-radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    background-image: radial-gradient(top, circle cover, hsl(244, 31%, 53%) 0%, hsl(266, 61%, 42%) 80%);
    color: white;
}

@media screen and (max-width: 600px) {
    .discover-modal .modal {
        width: 90%;
        position: absolute;
        top: 60%;
    }
}

.discover-modal .modal > h3 {
    opacity: 0.7;
    font-size: 25px;
}

.discover-modal .modal > p {
    opacity: 0.7;
    font-size: 22px;
    line-height: 35px;
    font-weight: 400;
}

.discover-cover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: black;
    z-index: 1010;
    opacity: 0.5;
}


